import React from "react";
import ChampionIcon from "./champion-icon";

class CompView extends React.Component{
  render(){
    const items = [];
    for (let i=0; i<9; i++){
      items.push(
        <ChampionIcon name="yasuo"/>
      )
    }
    return(
      <div>
        <p>構成だよ～</p>
        {items}
      </div>
    )
  }
}

export default CompView;